import Header from "../components/Header";
import globalVars from "../library/globalVariables";
import axios from "axios";
import generateUniqueId from "generate-unique-id";

const Emails = ({ data }) => {

  const tableBody = data.map(email => (
    <tr key={generateUniqueId()}>
      <td key={generateUniqueId()}>{email.date}</td>
      <td key={generateUniqueId()}>{email.to}</td>
      <td key={generateUniqueId()}>{email.subject}</td>
      <td key={generateUniqueId()}>{email.message}</td>
    </tr>
  ));

  return (
    <>
      <Header />
      <table>
        <thead>
          <tr>
            <td>Date and time</td>
            <td>To</td>
            <td>Subject</td>
            <td>Message</td>
          </tr>
        </thead>
        <tbody>{tableBody}</tbody>
      </table>
    </>
  );
};

Emails.getInitialProps = async () => {
  const res = await axios({
    method: "get",
    url: `${globalVars.serverURL}/emails`,
    responseType: "json"
  });
  const data = await res.data;
  return { data };
};

export default Emails;
